import React, { useEffect } from "react";
import Sketch from "react-p5";
export default ({ background, lines }) => {
	class P4Vector {
		constructor(x, y, z, w) {
		  this.x = x;
		  this.y = y;
		  this.z = z;
		  this.w = w;
		}
	}
	let angle = 13;
	let points = [];
	let size = 25;
	const setup = (p5, canvasParentRef) => {
		p5.createCanvas(340, 340, p5.WEBGL).parent(canvasParentRef);
	};
	let distance = 60;
	let mult = 100;
	const draw = (p5) => {
		points = [];
		points[0] = new P4Vector(-size, -size, -size, size);
		points[1] = new P4Vector(size, -size, -size, size);
		points[2] = new P4Vector(size, size, -size, size);
		points[3] = new P4Vector(-size, size, -size, size);
		points[4] = new P4Vector(-size, -size, size, size);
		points[5] = new P4Vector(size, -size, size, size);
		points[6] = new P4Vector(size, size, size, size);
		points[7] = new P4Vector(-size, size, size, size);
		points[8] = new P4Vector(-size, -size, -size, -size);
		points[9] = new P4Vector(size, -size, -size, -size);
		points[10] = new P4Vector(size, size, -size, -size);
		points[11] = new P4Vector(-size, size, -size, -size);
		points[12] = new P4Vector(-size, -size, size, -size);
		points[13] = new P4Vector(size, -size, size, -size);
		points[14] = new P4Vector(size, size, size, -size);
		points[15] = new P4Vector(-size, size, size, -size);
		p5.background(background);
		p5.rotateX(p5.mouseY/100)
    	p5.rotateY(p5.mouseY/200)
		function connect(offset, i, j, points) {
			p5.strokeWeight(2);
			p5.stroke(lines);
			p5.line(points[i+offset][0], points[i+offset][1], points[i+offset][2], points[j+offset][0], points[j+offset][1], points[j+offset][2]);
		}
		let projected3d = [];
		for (let i = 0; i < points.length; i++) {
			p5.stroke(lines); 
			p5.strokeWeight(4);
			p5.noFill();
			let rotationXW = [
				[p5.cos(angle), 0, 0, -p5.sin(angle)],
				[0, 1, 0, 0],
				[0, 0, 1, 0],
				[p5.sin(angle), 0, 0, p5.cos(angle)]
			  ]
			  let rotatedArr = matmul4(rotationXW, points[i]);
			  let rotated = new P4Vector(rotatedArr[0], rotatedArr[1], rotatedArr[2], rotatedArr[3]);
			  
			  let w = 1/(distance-rotated.w);
			  let projection = [
				[w, 0, 0, 0],
				[0, w, 0, 0],
				[0, 0, w, 0]
			  ];
			  var projected = matmul(projection, rotated);
			  projected3d[i] = projected;
			  p5.point(projected[0], projected[1], projected[2]);
			}
			for (let i = 0; i < 4; i++) {
			  connect(0, i, (i+1)%4, projected3d);
			  connect(0, i+4, ((i+1)%4)+4, projected3d)
			  connect(0, i, i+4, projected3d)
			}
			for (let i = 0; i < 4; i++) {
			  connect(8, i, (i+1)%4, projected3d);
			  connect(8, i+4, ((i+1)%4)+4, projected3d)
			  connect(8, i, i+4, projected3d)
			}
			for (let i = 0; i < 8; i++) {
			  connect(0, i, i+8, projected3d);
			}
			angle = p5.mouseX/100;
			
		};

		function matmul(projection, points) {
			let res = [0, 0, 0];
			for (let i = 0; i < projection.length; i++) {
			res[i] += projection[i][0] * points.x;
			res[i] += projection[i][1] * points.y;
			res[i] += projection[i][2] * points.z;
			res[i] += projection[i][3] * points.w;
			res[i] *= mult;
			}
			return res;
		}
	  
		function matmul4(projection, points) {
			let res = [0, 0, 0, 0];
			for (let i = 0; i < 4; i++) {
			res[i] += projection[i][0] * points.x;
			res[i] += projection[i][1] * points.y;
			res[i] += projection[i][2] * points.z;
			res[i] += projection[i][3] * points.w;
			}
			return res;
		}
	return <Sketch setup={setup} draw={draw} />;
};
