import { Box } from "@mui/system";
import { Typography, Card, CardContent, Grid, Chip, LinearProgress, Link } from "@mui/material";
import useFetch from "./useFetch";
import { useEffect, useState } from "react";
import { useHistory } from 'react-router-dom';
const Projects = () => {
    const { data, isLoading, error } = useFetch("https://raw.githubusercontent.com/0xKilty/0xKilty.github.io/main/data/projects.json")
    var posts = null;
    const [realPosts, setRealPosts] = useState(null);
    const [activeTag, setActiveTag] = useState("");
    var tagList = [];
    if (data && tagList.length === 0) {
      data.posts.map((post) => {
        post.tags.split(", ").map((tag) => {
          if (!tagList.includes(tag)) {
            tagList.push(tag);
          }
        })
      })
      posts = data.posts
    }
    if (realPosts == null && data) {
      setRealPosts(data.posts)
    }
    const handleClickAll = (tag) => {
        if (activeTag === tag) {
          setActiveTag("");
          posts = data.posts
        } else {
          setActiveTag(tag);
          posts = posts.filter((post) => post.tags.split(", ").includes(activeTag))
        }
    } 
    const linkStyle = {
      color:"secondary.contrastText"
    }
    useEffect(() => {
      if (data) {
        if (activeTag === "") {
          posts = data.posts;
        } else {
          posts = posts.filter((post) =>
            post.tags.split(", ").includes(activeTag)
          );
        }
      }
      setRealPosts(posts)
    }, [activeTag])
    
    const history = useHistory();
    const handleTabClick = (tab) => {
      history.push(tab)
      window.scrollTo(0, 0);
    }
    return (
      <Box
        sx={{
          width: "100%",
          height: "100vh",
          backgroundColor: "primary.dark",
          display: "block",
        }}
      >
        
        <Box sx={{ width: "100%", paddingTop: "11vh" }}>
        <Card
        sx={{
          marginX: "10vw",
          backgroundColor:"secondary.main",
          p:4
        }}
        >
          <Typography
            variant="header"
            color="secondary.contrastText"
          >
            Projects
          </Typography>
          <Typography
            variant="body1"
            color="secondary.contrastText"
            sx={{
              marginTop: "1vh",
            }}
          >
            All of these projects are avalible on my{" "}
            <Link
              href="https://github.com/0xKilty"
              target="_blank"
              color="secondary.contrastText"
            >
              github
            </Link>
            .
          </Typography>
          {isLoading && <LinearProgress />}
          { data && 
            <Box
            sx={{
              marginTop: "2vh",
            }}
          >
            {tagList.map((tag) => {
                if (tag === activeTag) {
                  return (
                    <Chip
                      label={<Typography variant="chip" color="primary.contrastText">{tag}</Typography>}
                      onClick={() => handleClickAll(tag)}
                      sx={{ 
                        marginX: "3px", 
                        backgroundColor: "primary.dark",
                        "&:hover": {
                          backgroundColor: "primary.light",
                        },
                      }}
                      variant="filled"
                    />
                  );
                }
                return (
                  <Chip
                    label={<Typography variant="chip" color="secondary.contrastText">{tag}</Typography>}
                    onClick={() => handleClickAll(tag)}
                    sx={{ 
                      marginX: "3px", 
                      backgroundColor: "secondary",
                      "&:hover": {
                        backgroundColor: "secondary.main",
                      },
                    }}
                    variant="filled"
                  />
                );
            })}
          </Box>
          }
        </Card>
          <Box
            sx={{
              alignItems: "center",
              width: "80%",
              margin: "auto",
              marginTop: "3vh",
            }}
          >
            <Grid container rowSpacing={1} columnSpacing={3}>
              {realPosts && 
                realPosts.map((post) => {
                  return (
                    <Grid item>
                      <Card
                        sx={{
                          width: "400px",
                          backgroundColor: "secondary.main",
                          maxHeight: "50vh",
                          display: "block",
                        }}
                      >
                        <CardContent>
                          <Typography
                            variant="artSub"
                            color="secondary.contrastText"
                            sx={{
                              marginBottom: "10vh",
                            }}
                          >
                            <Link 
                              color="secondary.contrastText"
                              onClick={() => handleTabClick(`/Projects/${post.title}`)}
                            >
                              {post.title}
                            </Link>
                          </Typography>
                          <br />
                          <br />
                          <Typography
                            variant="body1"
                            sx={{ mb: 1.5, whiteSpace: "nowrap" }}
                            color="secondary.contrastText"
                          >
                            {post.description}
                          </Typography>
                          <Box>
                            {post.tags.split(", ").map((value) => (
                              <Chip
                                label={
                                  <Typography
                                    variant="chip"
                                    sx={{ color: "primary.contrastText" }}
                                  >
                                    {value}
                                  </Typography>
                                }
                                onClick={() => handleClickAll(value)}
                                sx={{
                                  marginX: "3px",
                                  backgroundColor: "primary.dark",
                                  "&:hover": {
                                    backgroundColor: "primary.veryLight",
                                    textColor: "secondary.contrastText",
                                  },
                                }}
                              />
                            ))}
                          </Box>
                          <br />
                          <Typography
                            variant="body1"
                            color="secondary.contrastText"
                          >
                            {post.date}
                          </Typography>
                        </CardContent>
                      </Card>
                    </Grid>
                  );})}
            </Grid>
          </Box>
        </Box>
      </Box>
    );
}
 
export default Projects;