import './App.css';
import NavBar from './Navbar/NavBar';
import Home from './Home/Home'
import { createTheme, ThemeProvider } from '@mui/material'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { useState } from 'react';
import Overviews from './Home/Overviews';
import AboutMe from './AboutMe';
import Footer from './Footer';
import WebDev from './WebDev';
import Projects from './Projects';
import NotFound from './NotFound';
import Blog from './Blog';
import Article from './Article';
import Resume from './Resume';
const blueNavy = [
  {
    main:'#add8e6',
    veryLight:'#cde7f0',
    light:'#bddfeb',
    dark:'#8aacb8',
    contrastText:'#000'
  },
  {
    main:'#00008B',
    light:'#00006f',
    contrastText:'#fff'
  }
]
const peachOrange = [
  {
    main:'#FCEDDA',
    veryLight:'#fdf4e8',
    light:'#fcf0e1',
    dark:'#e2d8ca',
    contrastText:'#000'
  },
  {
    main:'#EE4E34',
    light:'#f1715c',
    contrastText:'#fff'
  }
]
const redWhite = [
  {
    main:'#990011',
    veryLight:'#c16670',
    light:'#ad3240',
    dark:'#7a000d',
    contrastText:'#fff'
  },
  {
    main:'#FCF6F5',
    light:'#fcf7f7',
    contrastText:'#000000'
  }
]
const bluePeach = [
  {
    main:'#00539C',
    veryLight:'#6697c3',
    light:'#3275af',
    dark:'#00427c',
    contrastText:'#fff'
  },
  {
    main:'#EEA47F',
    light:'#F1B698',
    contrastText:'#ffffff'
  }
]
const blackOrange = [
  {
    main: "#2f2f2e",
    veryLight:'#585857',
    light:'#434342',
    dark:'#1c1c1b',
    contrastText:'#ffffff'
  },
  {
    main:'#ff6f28',
    light:'#ff7d3d',
    contrastText:'#ffffff'
  }
]
const initTheme = createTheme({
  palette:{
    primary:blackOrange[0],
    secondary:blackOrange[1]
  },
  typography:{
    title:{
      fontWeight:700,
      fontSize: 24
    },
    tab:{
      fontSize: 18,
      textTransform: 'capitalize',
    },
    header: {
      fontSize: 48,
      fontWeight:700,
    },
    body1:{
      fontSize: 18,
      fontWeight: 400
    },
    artTitle:{
      fontWeight:700,
      fontSize: 82,
    },
    artDes:{
      fontWeight:300,
      fontSize: 24,
    },
    artCred:{
      fontWeight:200,
      fontSize: 18,
    },
    artHeader:{
      fontWeight:500,
      fontSize: 62,
    },
    artSub:{
      fontWeight:300,
      fontSize: 36,
    },
    artSubSub:{
      fontWeight:200,
      fontSize: 28,
    },
    artSubHeading:{
      fontWeight:200,
      fontSize: 14,
    },
    artCode:{
      fontWeight:300,
      fontSize: 15,
      font:"Roboto",
    },
    chip:{
      fontWeight:300,
      fontSize: 15,
      "&:hover": {
        color: "secondary.contrastText",
      },
    },
  },
  
});

function App() {
  const [theme, setTheme] = useState(initTheme)
  const [rerender, setRerender] = useState(true);
  function changeTheme(color) {
    if (color === 'black/orange') {
      initTheme.palette.primary = blackOrange[0];
      initTheme.palette.secondary = blackOrange[1];
      setTheme(initTheme);
      setRerender(!rerender)
    }
    else if (color === 'blue/peach') {
      initTheme.palette.primary = bluePeach[0];
      initTheme.palette.secondary = bluePeach[1];
      setTheme(initTheme);
      setRerender(!rerender)
    }
    else if (color === 'red/white') {
      initTheme.palette.primary = redWhite[0];
      initTheme.palette.secondary = redWhite[1];
      setTheme(initTheme);
      setRerender(!rerender)
    }
    else if (color === 'peach/orange') {
      initTheme.palette.primary = peachOrange[0];
      initTheme.palette.secondary = peachOrange[1];
      setTheme(initTheme);
      setRerender(!rerender)
    }
    else if (color === 'blue/navy') {
      initTheme.palette.primary = blueNavy[0];
      initTheme.palette.secondary = blueNavy[1];
      setTheme(initTheme);
      setRerender(!rerender)
    }
  }
  let background = theme.palette.primary.dark
  let lines = theme.palette.primary.contrastText
  return (
    <ThemeProvider theme={theme}>
      <Router>
        <Switch>
          <Route exact path="/">
            <Home background={background} lines={lines} />
            <Overviews />
            <NavBar changeTheme={changeTheme} current="Home" />
          </Route>
          <Route path="/Home">
            <Home background={background} lines={lines} />
            <Overviews />
            <NavBar changeTheme={changeTheme} current="Home" />
          </Route>
          <Route exact path="/Projects">
            <Projects />
            <NavBar changeTheme={changeTheme} current="Projects" />
            <Footer />
          </Route>
          <Route path="/Projects/:title">
            <Article category="Projects" />
            <NavBar changeTheme={changeTheme} current="Projects" />
            <Footer />
          </Route>
          <Route path="/Web Dev">
            <NavBar changeTheme={changeTheme} current="Web Dev" />
            <WebDev />
            <Footer />
          </Route>
          <Route path="/About Me">
            <AboutMe />
            <NavBar changeTheme={changeTheme} current="About Me" />
            <Footer />
          </Route>
          <Route path="/Blog/:title">
            <Article category="Blog" />
            <NavBar changeTheme={changeTheme} current="Blog" />
            <Footer />
          </Route>
          <Route path="/Blog">
            <NavBar changeTheme={changeTheme} current="Blog" />
            <Blog />
            <Footer />
          </Route>
          <Route path="/Resume">
            <Resume />
            <Footer />
            <NavBar changeTheme={changeTheme} current="Resume" />
          </Route>
          <Route exact path="*">
            <NotFound />
          </Route>
        </Switch>
      </Router>
    </ThemeProvider>
  );
}

export default App;
