import { Button, Menu } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import { Box } from '@mui/material';
import { useState } from 'react';
import {Typography} from "@mui/material";
const ThemePicker = ( { changeTheme }) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleMenuClick = (event) => {
        setAnchorEl(event.currentTarget);
    }
    const handleClose = (color) => {
        setAnchorEl(null);
        changeTheme(color)
    }
    return ( 
        <Box>
            <Button 
            variant='outlined' 
            color='secondary' 
            id="basic-button"
            aria-controls={open ? 'basic-menu' : undefined}
            aria-haspopup="true"
            aria-expanded={open ? 'true' : undefined}
            sx={{
                justifyItems:'center',
                fontWeight:'bold',
                display:'flex',
                borderRadius:2, 
                p:0.50
            }}
            onClick={ handleMenuClick }
            >
                <Typography
                variant='tab'
                color='primary.contrastText'
                >
                    Theme
                </Typography>
            </Button>
            <Menu
            id="basic-menu"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            MenuListProps={{
                'aria-labelledby': 'basic-button',
            }}
            sx={{
                color:'primary.dark'
            }}
            >
                <MenuItem 
                onClick={() => handleClose('black/orange')} 
                sx={{
                    color:'black'
                }}
                >Black/Orange</MenuItem>
                <MenuItem 
                onClick={() => handleClose('blue/peach')} 
                sx={{
                    color:'black'
                }}
                >Blue/Peach</MenuItem>
                <MenuItem 
                onClick={() => handleClose('red/white')} 
                sx={{
                    color:'black'
                }}
                >Red/White</MenuItem>
                <MenuItem 
                onClick={() => handleClose('peach/orange')} 
                sx={{
                    color:'black'
                }}
                >Peach/Orange</MenuItem>
                <MenuItem 
                onClick={() => handleClose('blue/navy')} 
                sx={{
                    color:'black'
                }}
                >Blue/Navy</MenuItem>
            </Menu>
        </Box>
     );
}
 
export default ThemePicker;