import Toolbar from '@mui/material/Toolbar';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import { useState } from 'react';
import Tabs from './Tabs';
import { useHistory } from 'react-router-dom';
import ThemePicker from './ThemePicker';
const initTabs = [
    {
        title:'Home',
        active:false,
        id:1,
    },
    {
        title:'Projects',
        active:false,
        id:2,
    },
    {
        title:'Web Dev',
        active:false,
        id:3,
    },
    {
        title:'About Me',
        active:false,
        id:4,
    },
    {
        title:'Blog',
        active:false,
        id:5,
    },
    {
        title:'Resume',
        active:false,
        id:6,
    }
];
const NavBar = ( { changeTheme, current } ) => {
    const [tabs, setTabs] = useState(initTabs);
    const history = useHistory();
    tabs.map((tab) => {
        if (tab.title === current) {
            tab.active = true
        } else {
            tab.active = false
        }
    })
    const handleTabClick = (tab) => {
        history.push(`/${tab}`)
        window.scrollTo(0, 0);
    }
    
    return ( 
        <Box 
        className="navbar"
        sx={{
            margin:'auto',
            position:'fixed',
            display:'block'
        }}
        >
            <AppBar color='primary'>
                <Toolbar
                sx={{
                    alignItems: 'center',
                    display: 'flex',
                    justifyContent:'between'
                }}>
                    <Box 
                    className='iankilty'
                    >
                        <Typography variant='title' onClick={() => handleTabClick("Home")}>Ian Kilty</Typography>
                    </Box>
                    <Tabs tabs={ tabs } handleTabClick={ handleTabClick } />
                    <ThemePicker changeTheme={ changeTheme }/>
                </Toolbar>
            </AppBar>
        </Box>
     );
}
 
export default NavBar;