import { Box } from "@mui/system";
import { Typography, Card, CardContent, Link, Divider, LinearProgress } from "@mui/material";
import useFetch from "./useFetch";
const Blog = () => {
    const { data, isLoading, error } = useFetch("https://raw.githubusercontent.com/0xKilty/0xKilty.github.io/main/data/blogs.json")
    return (
      <Box
        sx={{
          width: "100%",
          height: "100vh",
          backgroundColor: "primary.dark",
          display: "flex",
        }}
      >
        <Box sx={{ width: "100%", paddingTop: "11vh" }}>
          <Box
            sx={{
              alignItems: "center",
              width: "60%",
              marginX: "auto",
              marginTop: "3vh",
            }}
          >
            <Card
            sx={{
              backgroundColor:"secondary.main",
              p:4
            }}
            >
              <Typography
                variant="header"
                sx={{
                  paddingBottom: "1vh",
                  display: "block",
                }}
                color="secondary.contrastText"
              >
                Blog
              </Typography>
              <Divider sx={{mb:2, backgroundColor:"primary.veryLight", pt:0.3}}/>
              <Card
                sx={{
                  width: "25vw",
                  minWidth: "200px",
                  backgroundColor: "primary.light",
                  maxHeight: "50vh",
                  display: "block",
                }}
              >
                <CardContent>
                  <Typography
                    variant="artSub"
                    color="primary.contrastText"
                    sx={{
                      marginBottom: "10vh",
                    }}
                  >
                    Contents
                  </Typography>
                  <br />
                  <br />
                  <Typography
                    variant="body1"
                    sx={{ mb: 1.5 }}
                    color="primary.textContrast"
                  >
                    
                    {data &&
                      Object.keys(data).map((key) => (
                        <Box color="primary.contrastText">
                        <li>
                          <Link href={`#${key}`} color="primary.contrastText">
                            {key}
                          </Link>
                        </li>
                        </Box>
                      ))}
                  </Typography>
                </CardContent>
              </Card>
            </Card>
            <Box sx={{ paddingTop: "3vh" }}>
            {isLoading && <LinearProgress />}
              {data &&
                Object.entries(data).map(([key1, value1]) => (
                  <Typography
                    color="primary.contrastText"
                    variant="artSub"
                    sx={{ paddingTop: "30px", display: "block" }}
                    id={key1}
                  >
                    {key1}
                    {Object.entries(value1).map(([key2, value2]) => (
                      <Typography
                        color="primary.contrastText"
                        variant="body1"
                        sx={{ paddingTop: "10px", marginLeft: "25px" }}
                      >
                        <Link
                          href={`/Blog/${value2.title}`}
                          color="primary.contrastText"
                        >
                          <li>
                            <b>{value2.title}</b> - {value2.date} -{" "}
                            <span sx={{ whiteSpace: "nowrap" }}>
                              {value2.description}
                            </span>
                          </li>
                        </Link>
                      </Typography>
                    ))}
                  </Typography>
                ))}
            </Box>
          </Box>
        </Box>
      </Box>
    );
}
 
export default Blog;