import { useState } from "react";
import useWindowSize from "../useWindowSize";
import MenuIcon from '@mui/icons-material/Menu';
import { IconButton, Menu, MenuItem, Box, Button, Typography, Stack } from '@mui/material';
const Tabs = ({ tabs, handleTabClick }) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    const handleMenuClick = (event) => {
        setAnchorEl(event.currentTarget);
    }
    const handleClose = (tab) => {
        setAnchorEl(null);
        handleTabClick(tab);
    }
    const [width, height] = useWindowSize()
    function isActive(tab) {
        if (tab.active) {
            return {
                justifyItems:'center',
                fontWeight:'bold',
                display:'flex',
                borderRadius:3,
                p: 0.57,
                color:"secondary.contrastText",
                backgroundColor:'secondary.main',
                '&:hover': {
                    backgroundColor:'secondary.light',
                }
            }
        }
        return {
            justifyItems:'center',
            fontWeight:'bold',
            display:'flex',
            color:"primary.contrastText",
            borderRadius:3,
            p: 0.57,
            '&:hover': {
              backgroundColor:'secondary.light',
              color:"secondary.contrastText",
            }
        }
    }
    const smallUnit = 822;
    var small = (width <= smallUnit);
    return (
      <Box
        sx={{
          display: "flex",
          justifyItems: "center",
          textAlign: "center",
          alignItems: "center",
          margin: "auto",
        }}
      >
        {!small && (
          <Stack
            spacing={3}
            direction="row"
            className="tabs"
            sx={{
              display: "flex",
              justifyItems: "center",
              textAlign: "center",
              alignItems: "center",
              margin: "auto",
            }}
          >
            {tabs.map((tab) => (
              <Button
                variant="text"
                color="secondary"
                sx={isActive(tab)}
                onClick={() => handleTabClick(tab.title)}
                key={tab.id}
              >
                <Typography variant="tab">{tab.title}</Typography>
              </Button>
            ))}
          </Stack>
        )}
        {small && (
          <Stack>
            <IconButton
              aria-label="Menu"
              color="secondary"
              onClick={handleMenuClick}
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="basic-menu"
              anchorEl={anchorEl}
              open={open}
              onClose={handleClose}
              MenuListProps={{
                "aria-labelledby": "basic-button",
              }}
              sx={{
                color: "primary.dark",
              }}
            >
              <MenuItem
                onClick={() => handleClose("Home")}
                sx={{
                  color: "black",
                }}
              >
                Home
              </MenuItem>
              <MenuItem
                onClick={() => handleClose("Projects")}
                sx={{
                  color: "black",
                }}
              >
                Projects
              </MenuItem>
              <MenuItem
                onClick={() => handleClose("Web Dev")}
                sx={{
                  color: "black",
                }}
              >
                Web Dev
              </MenuItem>
              <MenuItem
                onClick={() => handleClose("About Me")}
                sx={{
                  color: "black",
                }}
              >
                About Me
              </MenuItem>
              <MenuItem
                onClick={() => handleClose("Blog")}
                sx={{
                  color: "black",
                }}
              >
                Blog
              </MenuItem>
              <MenuItem
                onClick={() => handleClose("Resume")}
                sx={{
                  color: "black",
                }}
              >
                Resume
              </MenuItem>
            </Menu>
          </Stack>
        )}
      </Box>
    );
}
 
export default Tabs;