import { useParams } from "react-router-dom";
import { Box } from "@mui/system";
import { Typography, Link, Breadcrumbs, LinearProgress } from "@mui/material";
import { useState } from "react";
import ArtComp from "./ArtComp";
import { useHistory } from "react-router-dom";
const Article = ({category}) => {
  const { title } = useParams();
  const [data, setData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const history = useHistory();
  console.log(title)
  fetch("https://raw.githubusercontent.com/0xKilty/0xKilty.github.io/main/posts/" + title + ".saff")
    .then((response) => response.body)
    .then((rb) => {
      const reader = rb.getReader();

      return new ReadableStream({
        start(controller) {
          function push() {
            reader.read().then(({ done, value }) => {
              if (done) {
                controller.close();
                return;
              }
              controller.enqueue(value);
              push();
            });
          }

          push();
        },
      });
    })
    .then((stream) =>
      new Response(stream, {
        headers: { "Content-Type": "text/html" },
      }).text()
    )
    .then((result) => {
      setIsLoading(false);
      setData(result);
    });
  var headData = {};
  if (data) {
    var arr = data.split("\n");
    for (var line in arr) {
      if (arr[line].slice(0, 2) === "_(") {
        headData[arr[line].slice(2).split(":")[0]] = arr[line]
          .slice(2)
          .split(":")[1];
      }
    }
    while (arr.includes("_@")) {
      var test = arr.splice(arr.indexOf("_@")+1, arr.indexOf("@_")-arr.indexOf("_@")-1);
      for (var i = 0; i < arr.indexOf("@_")-arr.indexOf("_@"); i++) {
        arr.splice(arr.indexOf("_@")+1, 1);
      }
      arr[arr.indexOf("_@")] = "_~" + test.join("|");
      arr.splice(arr.indexOf("@_"), 1)
    }
  }
  
  return (
    <Box
      sx={{
        margin: "auto",
        height: "auto",
        backgroundColor: "primary.dark",
      }}
    >
      <Box
        sx={{
          paddingTop: "8vh",
          width: "60%",
          margin: "auto",
          height: "auto",
          minHeight: "100vh",
          backgroundColor: "primary.main",
          display: "block",
        }}
      >
        <Box
          sx={{
            px: 7,
            py: 3
          }}
        >
          <Breadcrumbs aria-label="breadcrumb" color="primary.contrastText" sx={{pb:2}}>
            <Link underline="hover" color="primary.contrastText" href={`/${category}`}>
              { category }
            </Link>
            <Typography color="primary.contrastText">{ title }</Typography>
          </Breadcrumbs>
          <Typography
            variant="artTitle"
            sx={{
              marginLeft: "0vw",
            }}
            color="primary.contrastText"
          >
            {headData.title}
          </Typography>
          <Box sx={{ marginTop: "2vh" }}>
            <Typography variant="artDes" color="primary.contrastText">
              {headData.description}
            </Typography>
          </Box>
          <Box sx={{ marginTop: "2vh" }}>
            <Typography variant="artCred" color="primary.contrastText">
              {headData.date} - {headData.author}
            </Typography>
          </Box>
          <Box
            sx={{
              marginTop: "2vh",
              lineHeight: "1.5",
            }}
          >
            <Box>
              {isLoading && <LinearProgress />}
              {data &&
                arr.map((line, key) => <ArtComp line={line} key={key} />)}
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}
 
export default Article;