import { Box } from "@mui/system";
import { Typography, Card, CardContent, Link, Accordion, AccordionSummary, AccordionDetails, Divider } from "@mui/material";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import CodeIcon from '@mui/icons-material/Code';
import Image from "mui-image";

const ArtComp = ({line, unit}) => {
    if (line[0] === "_") {
      switch (line[1]) {
        case "*":
          if (line[3] === "*") {
            return (
              <Box key={unit}>
                <Typography variant="artSubSub" color="primary.contrastText">
                  {line.slice(4, line.length - 4)}
                </Typography>
                <br />
              </Box>
            );
          } else if (line[2] === "*") {
            return (
              <Box key={unit}>
                <Typography variant="artSub" color="primary.contrastText">
                  {line.slice(3, line.length - 3)}
                </Typography>
                <br />
              </Box>
            );
          } else {
            return (
              <Box key={unit}>
                <Typography variant="artHeader" color="primary.contrastText">
                  {line.slice(2, line.length - 2)}
                </Typography>
                <br />
              </Box>
            );
          }
        case "%":
          console.log('reached')
          let comp = line.slice(2).split(":");
          console.log(comp)
          return (
            <Accordion
              sx={{
                backgroundColor: "primary.light",
              }}
              key={unit}
            >
              <AccordionSummary
                expandIcon={
                  <ExpandMoreIcon sx={{ color: "primary.contrastText" }} />
                }
                aria-controls="panel1a-content"
                id="panel1a-header"
              >
                <Typography variant="body1" color="primary.contrastText">{comp[0]}</Typography>
              </AccordionSummary>
              <AccordionDetails>
                <Typography variant="body1" color="primary.contrastText">{comp[1]}</Typography>
              </AccordionDetails>
            </Accordion>
          );
        case ".":
          return (
            <Typography variant="body1" color="primary.contrastText" key={unit}>
              
                <li>{line.slice(2)}</li>
              
            </Typography>
          );
        case "?":
          return (
            <Image
              src={line.slice(2)}
              duration={0}
              style={{ width: "80%" }}
              key={unit}
            ></Image>
          );
        case "-":
          return (
            <Box key={unit}>
              <Divider>
                <CodeIcon sx={{ color: "primary.contrastText" }} />
              </Divider>
            </Box>
          );
        case "+":
          var note = line.slice(2).split(":");
          return (
            <Card
              color="primary.light"
              sx={{ backgroundColor: "primary.light", p: 0 }}
              key={unit}
            >
              <CardContent color="primary.main" sx={{ paddingLeft: "1vw" }}>
                <Typography variant="artSubSub" color="primary.contrastText">
                  {note[0]}
                </Typography>
                <br />
                <Typography variant="body1" color="primary.contrastText">
                  {note[1]}
                </Typography>
              </CardContent>
            </Card>
          );
          case "$":
            var url = line.slice(2, line.length-2).split("|")
            return (
                <Link href={url[0]} color="primary.contrastText" variant='body1' key={unit}>{url[1]}</Link>
            )
          case "#":
            return <br  key={unit} />;
          case "~":          
            return (
              <Card
                color="primary.light"
                sx={{ backgroundColor: "primary.light", p: 0 }}
                key={unit}
              >
                <CardContent color="primary.main" sx={{ paddingLeft: "1vw" }}>
                  <Typography variant="artCode" color="primary.contrastText">
                    <pre>
                      {
                        line.slice(2).split("|").map((code) => (
                          <span key={unit}>{code}<br /></span>
                        )) 
                      }
                    </pre>
                  </Typography>
                </CardContent>
              </Card>
            );
      }   

      
    } else {
        if (line.includes("_$")) {
            var a = line.split("$_")
            var b = []
            for (var i = 0; i < a.length; i++) {
              b[i] = a[i].split("_$")
            }
            return (
              <Typography
                variant="body1"
                color="primary.contrastText"
                key={unit}
              >
                <span key={unit}>
                  {b.slice(0, b.length - 1).map((c) => (
                    <span>
                      {c[0]}
                      {
                        <Link
                          color="primary.contrastText"
                          target="_blank"
                          rel="noreferrer"
                          href={c[1].split("|")[0]}
                          key={unit}
                        >
                          {c[1].split("|")[1]}
                        </Link>
                      }
                    </span>
                  ))}
                  {b[b.length - 1]}
                </span>
              </Typography>
            );
        } else {
          return (
            <Typography
                variant="body1"
                color="primary.contrastText"
                key={unit}
              >
                {line}
              </Typography>
          )
        }
    }
}
 
export default ArtComp;